

RegExp.escape = function escape (text )  {
  if (!escape.sRE) {
    var specials = [
      '/', '.', '*', '+', '?', '|',
      '(', ')', '[', ']', '{', '}', '$', '^', '\\'
    ];
    escape.sRE = new RegExp(
      '(\\' + specials.join('|\\') + ')', 'g'
    );
  }
  return isString(text)?text.replace(escape.sRE, '\\$1')
                       :(isArray(text)?text.map(RegExp.escape).join("|")
                                      :"");
}



